var Drums = document.getElementById("Drums")
var Dubstep_1 = document.getElementById("Dubstep_1")

var audio_file = new Audio('../Sounds/Drums.ogg')
audio_file.addEventListener('timeupdate', function(){
    var buffer = .44
    if(this.currentTime > this.duration - buffer){
        this.currentTime = 0
        this.play()
    }
});




